.VisualValidator {
  width: 100%;
  height: 100%;
  flex-basis: 100%;
  padding: 1rem;
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  font-size: 1.125rem;
  line-height: 1.5;
  box-sizing: border-box;

  background-color: inherit;
  color: black;

  .--correct {
    background-color: #d7f5ce;
  }
  
  .--incorrect {
    background-color: #ff9ca5;
  }

  .--more {
    background-color: #fff1a9;
    &::after {
      content: '...';
      padding: 0 0.25rem;
      color: rgba(0, 0, 0, 0.5);
      user-select: none;
      font-size: 0.25rem;
    }
  }
}

@media (max-width: 600px) {
  .VisualValidator {
    font-size: 1rem;
  }
}