.OutputTextArea {
  width: 100%;
  height: 100%;
  flex-basis: 100%;

  textarea {
    box-sizing: border-box;
    padding: 1rem;
    width: 100%;
    height: 100%;
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: 1.125rem;
    line-height: 1.5;
    box-sizing: border-box;
    border: 0;
    background-color: inherit;
    color: black;

    resize: none;

    outline: none;
  }
}

@media (max-width: 600px) {
  .OutputTextArea {
    textarea {
      font-size: 1rem;
    }
  }
}