@import './../../styles/theme.scss';

.TabLayout {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.TabLayout__Toolbar {
  display: flex;
  width: 100%;
  background-color: #333333;
}

.TabLayout__Content {
  display: flex;
  flex-basis: 100%;
  width: 100%;
  background-color: #333333;
  overflow: auto;

  & > * {
    display: none;
    width: 100%;
    background-color: white;
    overflow: auto;

    &.--active {
      display: block;
    }
  }
}

.TabLayout__TabButton {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3.5rem;
  flex-basis: 100%;
  font-size: 0.875rem;
  font-weight: bolder;
  text-decoration: none;
  color: black;
  padding-right: 1rem;
  letter-spacing: 0.0625rem;
  color: rgb(180, 180, 175);
  background-color: #2e2e2e;
  box-shadow: inset 0 2px 2px 0 rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgb(238, 237, 228);
  cursor: pointer;
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  & > i {
    margin-right: 0.5rem;
  }

  &.--active {
    background-color: white;
    box-shadow: inset 0 3px 0 0 map-get($theme-colors, primary);
    cursor: default;

    /* Hack to fix color for the first child to match the code theme. */
    &:first-child {
      background-color: rgb(245, 242, 240);
      border-bottom-color: rgb(230, 227, 225);
    }

    & > i {
      color: map-get($theme-colors, primary);
    }

    & > span {
      color: black;
    }
  }
}

.TabLayout.--windowed {
  .TabLayout__TabButton {
    background-color: white;
    box-shadow: inset 0 3px 0 0 map-get($theme-colors, primary);
    cursor: default;

    justify-content: flex-start;
    padding-left: 1rem;

    margin-right: 1px;

    /* Hack to fix color for the first child to match the code theme. */
    &:first-child {
      background-color: rgb(245, 242, 240);
      border-bottom-color: rgb(235, 232, 230);
    }

    &:last-child {
      margin-right: 0;
    }

    & > i {
      color: map-get($theme-colors, primary);
    }

    & > span {
      color: black;
    }
  }
  
  .TabLayout__Content {
    & > * {
      display: block;
      margin-right: 1px;
  
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
