@import './../../styles/theme.scss';

.PlayPage {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}

.PlayPage__PracticeTitle {
  margin: 0 auto;
  text-align: center;
  user-select: none;

  h3 {
    font-size: 1.75rem;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 0.125rem;
    color: #cccccc;
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.25);
    line-height: 1;
  }
  h4 {
    font-size: 0.875rem;
    line-height: 1;
    letter-spacing: 0.0625rem;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5);
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.25);
  }
}

.PlayPage__Button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0.25rem;
  padding-right: 0.75rem;
  width: 100%;
  height: 3.25rem;
  text-decoration: none;
  color: map-get($theme-colors, on-warning);
  background-color: map-get($theme-colors, warning);
  box-shadow: 0 -1px 0 0 white;
  text-shadow: 0 1px 0 white;

  font-size: 0.875rem;
  text-transform: uppercase;
  font-weight: 700;

  user-select: none;

  &:first-child {
    margin-left: 0.25rem;
  }

  &:hover {
    background-color: lighten(map-get($theme-colors, warning), 10%);
  }

  i {
    margin-right: 0.25rem;
    font-size: 2rem;
    color: darken(map-get($theme-colors, warning), 40%);
  }

  &.--subtle {
    background-color: #efefef;
    color: rgba(0, 0, 0, 0.75);

    i {
      color: rgba(0, 0, 0, 0.3);
    }

    &:hover {
      background-color: #fafafa;
    }
  }

  &.--incorrect {
    position: relative;
    box-shadow: inset 3px 0 0 0 red, inset -3px 0 0 0 red, inset 0 -3px 0 0 red;
    &::after {
      content: 'INCORRECT';
      position: absolute;
      top: -1.25rem;
      height: 1.25rem;
      width: 100%;
      text-align: center;
      font-size: 0.75rem;
      line-height: 1.25rem;
      background-color: red;
      color: white;
      text-shadow: 0 1px 0 black;
      border-radius: 4px 4px 0 0;
    }
  }

  &.--correct {
    position: relative;
    box-shadow: inset 3px 0 0 0 green, inset -3px 0 0 0 green, inset 0 -3px 0 0 green;
    &::after {
      content: 'CORRECT';
      position: absolute;
      top: -1.25rem;
      height: 1.25rem;
      width: 100%;
      text-align: center;
      font-size: 0.75rem;
      line-height: 1.25rem;
      background-color: green;
      color: white;
      text-shadow: 0 1px 0 black;
      border-radius: 4px 4px 0 0;
    }
  }
}

@media (max-width: 400px) {
  .PlayPage__Button {
    span {
      font-size: 0.75rem;
    }
    i {
      font-size: 1.25rem;
    }
  }
}