@import './../../styles/theme.scss';

.IndexPage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem 0rem;

  height: 1px;
  min-height: 100vh;

  background-color: #efefef;
  overflow-x: hidden;

  h1, h2 {
    font-weight: 300;
    line-height: 1;
    user-select: none;
    text-shadow: 0 1px 0px white;
    text-align: center;
  }

  h1 {
    margin-right: -1.75rem;
    margin-bottom: 3rem;
    font-size: 5rem;
    text-transform: uppercase;
    letter-spacing: 1.75rem;
  }

  h2 {
    margin-right: -0.0625rem;
    font-size: 2rem;
    letter-spacing: 0.0625rem;
    color: rgba(0, 0, 0, 0.5);
    text-transform: lowercase;
  }

  .ToggleSwitch {
    margin-bottom: 2rem;
  }
}

.IndexPage__HighScore {
  display: flex;
  flex-direction: column;
  align-items: center;

  h3 {
    font-size: 0.75rem;
    font-weight: 700;
    text-transform: uppercase;
    color: rgba(0, 0, 0, 0.5);
  }
  h4 {
    font-size: 2rem;
    font-weight: 300;
    line-height: 1;
  }
}

.IndexPage__StartButton {
  display: block;
  margin-top: 2rem;
  padding: 1rem 0;
  width: 18.5rem;

  font-size: 0.875rem;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  color: map-get($theme-colors, on-primary);
  background-color: map-get($theme-colors, primary);

  border-radius: 999px;

  box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.25), inset 0 1px 0 0 rgba(255, 255, 255, 0.5);
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);

  &:hover {
    background-color: lighten(map-get($theme-colors, primary), 10%);
    transform: translateY(-1px);
    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1), inset 0 1px 0 0 rgba(255, 255, 255, 0.5), 0 3px 6px 0 rgba(0, 0, 0, 0.25);
  }

  &:active {
    background-color: darken(map-get($theme-colors, primary), 5%);
    box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.25),  0 1px 0 0 white;
    transform: translateY(1px);
  }
}
